@import url(//db.onlinewebfonts.com/c/0ea616b3e04eaacd28bc61964b5e5bfd?family=Brandish);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* import font */

.portfolio-section {
    height: 100vh;
    overflow: hidden;
}

.portfolio-comingSoon {
    display: flex;
    justify-content: center;
    align-items: center;
}

.portfolio-comingSoon > span {
    rotate: -33deg;
    font-size: 7vw;
}

.portfolio-header {
  font-family: Brandish;
  color: rgb(252,159,68);
}

  .Name {
    position: absolute;
    margin: -4% 0% 0% 19%;
    -webkit-animation: slide-left 0.8s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
    animation: slide-left 0.8s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
    
  }

  @-webkit-keyframes slide-left {
    0% {
      -webkit-transform: translate(0, 0) rotate(-29deg);
              transform: translate(0, 0) rotate(-29deg);
    }
    100% {
      -webkit-transform: translate(-8%, -8%) rotate(-29deg);
              transform: translate(-8%, 8%) rotate(-29deg);
    }
  }
  @keyframes slide-left {
    0% {
      -webkit-transform: translate(0, 0) rotate(-29deg);
              transform: translate(0, 0) rotate(-29deg);
    }
    100% {
      -webkit-transform: translate(-8%, -8%) rotate(-29deg);
              transform: translate(-8%, 8%) rotate(-29deg);
    }
  }

  .ProfileImg {
    height: max(45vh, 400px);
    box-shadow: #449afc8f 10px 10px 0px 0px;
    border: 5px solid #449bfc;
    border-radius: 20px;
  }

  .about-text {
    margin: 1vh 0vh 2.5vh 0vh;
    font-size: max(3vh, 20px);
  }
.OrangeNav, .PinkNav, .BlueNav {
    -webkit-clip-path: polygon(100% 0, 0 100%, 100% 100%);
            clip-path: polygon(100% 0, 0 100%, 100% 100%);
    position: absolute;
    right: 0;
    bottom: 0;
    overflow: inherit;
}

.NavItem {
    font-family: Brandish;
    font-size: 4vh;
    color: white;
}

.NavItem:hover {
    transition: .1s ease-in-out;
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    color: rgb(254,102,102);
    cursor: pointer;
}

.LogoLink {
    margin-left: 15%;
}

.LogoItem {
    height: 7vh;
}

.LogoItem:hover, .NavItem:hover  {
    -webkit-filter: invert(100%);
            filter: invert(100%);
    cursor: pointer;
}

.OrangeNav {
background-color: rgb(252,159,68);
height: 48vh;
width: 86.4vh;

}

.PinkNav {
background-color: rgb(254,102,102);
height: 38.4vh;
width: 69.12vh;
}

.BlueNav {
background-color: rgb(68,155,252);
height: 28.8vh;
width: 51.84vh;
}

.NavBarO {
    position: relative;
    top: 48%;
    left: 6%;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    -webkit-transform: rotate(-29deg);
            transform: rotate(-29deg);
}
  
.NavBarP {
position: relative;
top: 53%;
left: 0%;
display: flex;
flex-direction: row;
justify-content: center;
-webkit-transform: rotate(-29deg);
        transform: rotate(-29deg);
margin-right: 10%;
margin-top: 3%
}
.careerPath {
    stroke-dashoffset: 1000;
    -webkit-animation: dash 10s linear infinite forwards;
            animation: dash 10s linear infinite forwards
}

@-webkit-keyframes dash {
    to {
        stroke-dashoffset: 0;
    }
}

@keyframes dash {
    to {
        stroke-dashoffset: 0;
    }
}

.careerPoint:hover {
    transition: .1s ease-in-out;
    fill: rgb(252,159,68);
    height: 50px;
    width: 50px;
    cursor: pointer;
}

.careerNext:hover {
    cursor: pointer;
}

.careerInfoText {
    font-size: 24px;
}
